.Header {
	-webkit-user-select: none;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
}

.Div {
	-webkit-user-select: none;
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
}

.show {
	-webkit-animation: 1s ease-in-out 1 forwards move;
	        animation: 1s ease-in-out 1 forwards move;
	bottom: calc(var(--tabbar_height) + 5px);
}

.hide {
	animation: 0.5s ease-in-out 1 reverse forwards move;
}

@-webkit-keyframes move {
	from {
		bottom: 0;
	}
	,
	50% {
		bottom: calc(var(--tabbar_height) / 2);
	}
	to {
		bottom: calc(var(--tabbar_height) + 5px);
	}
}

@keyframes move {
	from {
		bottom: 0;
	}
	,
	50% {
		bottom: calc(var(--tabbar_height) / 2);
	}
	to {
		bottom: calc(var(--tabbar_height) + 5px);
	}
}

